.Upload_Overlay {
  background: transparent;
  align-items: center;
  display: flex;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 11;
}
.Upload_Overlay:hover {
  background: rgba(0, 0, 0, 0.5019607843);
  color: #fff;
  opacity: 1;
  padding: 6px 20px;
  border-radius: 4px;
  transition: 0.1s background ease-in;
}
.Upload_Overlay:hover input {
  cursor: pointer;
}

.image_preview {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.File_Preview {
  border: 2px dashed #b8bac2;
  border-radius: 8px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0.5;
  width: 100%;
}
.File_Preview div:nth-child(1) {
  -moz-column-gap: 8px;
       column-gap: 8px;
}
.File_Preview div:nth-child(1) p {
  font-family: Roboto;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
.File_Preview div:nth-child(1) svg {
  font-size: 40px;
}

.RemoveButton {
  box-sizing: border-box;
  border-radius: 8px;
  width: 72px;
  height: 37px;
  font-weight: 500;
  font-size: 13px;
  justify-content: center;
  color: #ffffff;
}
.RemoveButton:hover {
  text-decoration: unset;
}/*# sourceMappingURL=Styles.module.css.map */